<template lang="html">
  <div class="reschedule__section">
    <div class="NocoachAvailable--global" v-if="!sessionDetailCheck">
       <div class="NoAvailable">
          <p>Choose a session transaction to see it's content</p>
       </div>
    </div>
    <div class="reschedule" v-if="sessionDetailCheck">
      <div class="reschedule__name--title">
        Session Details
      </div>
      <div class="reschedule__avatar">
        <v-avatar size=68>
          <img
          :src="sessionDetail.user_data[0].avatar"
          alt="John"
          >
        </v-avatar>
      </div>
      <div class="reschedule__name">
        {{ sessionDetail.user_data[0].fullname }}
      </div>
      <div class="reschedule__scheduled">
        <h5>Scheduled in</h5>
        <div class="reschedule__scheduled__date">
          <span>{{ filteredDate }} (UTCH +7)</span>
          <h2>{{ filteredStart }} - {{ filteredEnd }}</h2>
        </div>
      </div>
      <div class="hr--90"></div>
      <div class="reschedule__btn">
        <v-btn
          depressed
          small
          :loading="btnLoading"
          color="#375197"
          style="color:#fff;"
          @click="changeDialogState"
        >
          Progress Report
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  data () {
    return {
      inputToken: 1,
      rating: 3
    }
  },
  computed: {
    showDialog () {
      return this.$store.state.showDialog
    },
    sessionDetailCheck () {
      const detailCheck = this.$store.state.sessionDetailCoach

      if (detailCheck == null) {
        return false
      } else {
        return true
      }
    },
    sessionDetail () {
      return this.$store.state.sessionDetailCoach
    },
    filteredDate () {
      return this.sessionDetail.appointment[0].date ? moment(this.sessionDetail.appointment[0].date).format('ddd, MMM DD, YYYY') : ''
    },
    filteredStart () {
      const time = this.sessionDetail.appointment[0].start_time;
      const formatted = moment(time, "HH:mm:ss").format("HH:mm");

      return formatted
    },
    filteredEnd () {
      const time = this.sessionDetail.appointment[0].end_time;
      const formatted = moment(time, "HH:mm:ss").format("HH:mm");

      return formatted
    },
    btnLoading () {
      return this.$store.state.loadingTrue
    }
  },
  methods: {
    changeDialogState () {
      this.$store.commit('toggleDialog');
    }
  }
}
</script>

<style lang="scss">

</style>



