<template lang="html">
  <div class="table__list">
    <div class="NocoachAvailable--global" v-if="!sessionListCheck">
       <div class="NoAvailable">
          <p>There's no upcoming session</p>
       </div>
    </div>
    <table style="width:100%" v-if="sessionListCheck">
      <tr>
        <th>Transaction</th>
        <th>Student</th>
        <th>Session Date</th>
        <th>Action</th>
      </tr>
      <tr
        v-for="(data, i) in sortedItems"
        :key="i"
        @click="getStudent(i, data.username, data.appointment_id)"
        :class="{'table--active': i == activeIndex}"
      >
        <td align="center">{{ data.created_at | formatDate }}</td>
        <td align="center">{{ data.student_name }}</td>
        <td align="center">{{ data.date | formatDate }}, {{ data.start_time | formatTime }} - {{ data.end_time | formatTime }}</td>
        <td align="center"><div class="semi__btn">View Details</div></td>
      </tr>
    </table>
  </div>
</template>

<script>
import { MomentFilter } from '../../../components/mixins/MomentFilter.js'
import axios from '../../../store/axios-auth'

export default {
  mixins: [MomentFilter],
  data () {
    return {
      activeIndex: null
    }
  },
  methods: {
    getStudent (i, username, appointment_id) {
      this.activeIndex = i

      // TO GET STUDENT DETAIL
      const studentData = {
        username: username,
        app_id: appointment_id
      }
      this.$store.dispatch('POST_SESSION_DETAIL_COACH', studentData)

      // TO GET STUDENT PROGRESS REPORT
      const studentUsername = {
        username: username
      }
      this.$store.dispatch('GET_CALL1_CALL2', studentUsername)
      this.$store.commit('buttonLoading');
    },
  },
  computed: {
    sortedItems() {
      function compare(a, b) {
        if (a.date < b.date)
          return 1;
        if (a.date > b.date)
          return -1;
        return 0;
      }

      return this.$store.state.DashUpcomingSessions.new_appointment.sort(compare);
    },
    sessionListCheck () {
      if (this.sortedItems != 0) {
        return true
      } else {
        return false
      }
    },
    StoreDataUpcoming(){
       return this.$store.state.DashUpcomingSessions.new_appointment
    },
  },
  created() {
    this.$store.dispatch('GET_DASH_UPCOMING_SESSIONS')
  }
}
</script>

<style lang="scss">
</style>



