<template lang="html">
  <div class="cert__prog">
    <div class="view__header">
      <div class="view__header__title">
        <div>
          <h3>A1 Progress</h3>
          <div class="blue__circle--light"></div>
        </div>
      </div>
    </div>
    <div class="cert__prog__container">
      <div class="cert__prog__study">
        <ul>
          <li :class="[ sessionTabs === 'fe' ? 'tab--active' : '']" @click="sessionTabs='fe'">
            <a>
              First English
            </a>
          </li>
          <li :class="[ sessionTabs === 'nde' ? 'tab--active' : '']" @click="sessionTabs='nde'">
            <a>
              New Dynamic English
            </a>
          </li>
        </ul>
      </div>
      <div class="cert__prog__unit">
        <div class="unit__container" v-if="sessionTabs === 'fe' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 1
            </div>
            <div class="unit__card__perc">
              {{ fe1 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 2
            </div>
            <div class="unit__card__perc">
              {{ fe2 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 3
            </div>
            <div class="unit__card__perc">
              {{ fe3 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 4
            </div>
            <div class="unit__card__perc">
              {{ fe4 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'nde' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Module 1
            </div>
            <div class="unit__card__perc">
              {{ nde1 }}%
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import CertificationUnit from './CertificationUnit'

export default {
  components: {
    // CertificationUnit
  },
  data() {
    return {
      sessionTabs: 'fe'
    }
  },
  computed: {
    fe1 () {
      const fe = this.$store.getters.fe
      const module = fe.filter(x => x.module === ' Unit 1 (Beginner)  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 12))
      if (val !== null) {
        return val
      }
      return 0
    },
    fe2 () {
      const fe = this.$store.getters.fe
      const module = fe.filter(x => x.module === ' Unit 2  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 14))
      if (val !== null) {
        return val
      }
      return 0
    },
    fe3 () {
      const fe = this.$store.getters.fe
      const module = fe.filter(x => x.module === ' Unit 3  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 14))
      if (val !== null) {
        return val
      }
      return 0
    },
    fe4 () {
      const fe = this.$store.getters.fe
      const module = fe.filter(x => x.module === ' Unit 4  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 14))
      if (val !== null) {
        return val
      }
      return 0
    },
    nde1 () {
      const nde = this.$store.getters.nde
      const module = nde.filter(x => x.module === ' Module 1  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 39))
      if (val !== null) {
        return val
      }
      return 0
    },
    toShowNodata () {
      if (this.fe1 && this.fe2 && this.fe3 && this.fe4 && this.nde1 == undefined) {
        return false
      } else {
        true
      }
    }
  }
}
</script>

<style lang="css">
</style>



