<template lang="html">
  <div class="cert__prog">
    <div class="view__header">
      <div class="view__header__title">
        <div>
          <h3>A2 Progress</h3>
          <div class="blue__circle--light"></div>
        </div>
      </div>
    </div>
    <div class="cert__prog__container">
      <div class="cert__prog__study">
        <ul>
          <li :class="[ sessionTabs === 'nde' ? 'tab--active' : '']" @click="sessionTabs='nde'">
            <a>
              New Dynamic English
            </a>
          </li>
          <li :class="[ sessionTabs === 'tls' ? 'tab--active' : '']" @click="sessionTabs='tls'">
            <a>
              The Lost Secret
            </a>
          </li>
        </ul>
      </div>
      <div class="cert__prog__unit">
        <div class="unit__container" v-if="sessionTabs === 'nde' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Module 2
            </div>
            <div class="unit__card__perc">
              {{ nde2 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Module 3
            </div>
            <div class="unit__card__perc">
              {{ nde3 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'tls' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Episode 1&2
            </div>
            <div class="unit__card__perc">
              {{ tls12 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Episode 3
            </div>
            <div class="unit__card__perc">
              {{ tls3 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Episode 4
            </div>
            <div class="unit__card__perc">
              {{ tls4 }}%
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import CertificationUnit from './CertificationUnit'

export default {
  components: {
    // CertificationUnit
  },
  data() {
    return {
      sessionTabs: 'nde'
    }
  },
  computed: {
    nde2 () {
      const nde = this.$store.getters.nde
      const module = nde.filter(x => x.module === ' Module 2  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 33))
      if (val !== null) {
        return val
      }
      return 0
    },
    nde3 () {
      const nde = this.$store.getters.nde
      const module = nde.filter(x => x.module === ' Module 3  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 35))
      if (val !== null) {
        return val
      }
      return 0
    },
    tls12 () {
      const tls = this.$store.getters.tls
      const module = tls.filter(x => x.module === ' Episode 1 & 2  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 5))
      if (val !== null) {
        return val
      }
      return 0
    },
    tls3 () {
      const tls = this.$store.getters.tls
      const module = tls.filter(x => x.module === ' Episode 3  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 5))
      if (val !== null) {
        return val
      }
      return 0
    },
    tls4 () {
      const tls = this.$store.getters.tls
      const module = tls.filter(x => x.module === ' Episode 4  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 5))
      if (val !== null) {
        return val
      }
      return 0
    },
  }
}
</script>

<style lang="css">
</style>



