<template lang="html">
  <div class="cert__prog">
    <div class="view__header">
      <div class="view__header__title">
        <div>
          <h3>B2 Progress</h3>
          <div class="blue__circle--light"></div>
        </div>
      </div>
    </div>
    <div class="cert__prog__container">
      <div class="cert__prog__study">
        <ul>
          <li :class="[ sessionTabs === 'nde' ? 'tab--active' : '']" @click="sessionTabs='nde'">
            <a>
              New Dynamic English
            </a>
          </li>
          <li :class="[ sessionTabs === 'dbe' ? 'tab--active' : '']" @click="sessionTabs='dbe'">
            <a>
              Dynamic Business English
            </a>
          </li>
          <li :class="[ sessionTabs === 'fib' ? 'tab--active' : '']" @click="sessionTabs='fib'">
            <a>
              Functioning In Business
            </a>
          </li>
          <li :class="[ sessionTabs === 'ebtn' ? 'tab--active' : '']" @click="sessionTabs='ebtn'">
            <a>
              English By The Numbers
            </a>
          </li>
        </ul>
      </div>
      <div class="cert__prog__unit">
        <div class="unit__container" v-if="sessionTabs === 'nde' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Module 5
            </div>
            <div class="unit__card__perc">
              {{ nde5 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Module 6
            </div>
            <div class="unit__card__perc">
              {{ nde6 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'dbe' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 2
            </div>
            <div class="unit__card__perc">
              {{ dbe2 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 3
            </div>
            <div class="unit__card__perc">
              {{ dbe3 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'fib' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Intro
            </div>
            <div class="unit__card__perc">
              {{ fibIntro }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Episode 1
            </div>
            <div class="unit__card__perc">
              {{ fib1 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'ebtn' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 3
            </div>
            <div class="unit__card__perc">
              {{ ebtn3 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 4
            </div>
            <div class="unit__card__perc">
              {{ ebtn4 }}%
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import CertificationUnit from './CertificationUnit'

export default {
  components: {
    // CertificationUnit
  },
  data() {
    return {
      sessionTabs: 'nde'
    }
  },
  computed: {
    nde5 () {
      const nde = this.$store.getters.nde
      const module = nde.filter(x => x.module === ' Module 5  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 35))
      if (val !== null) {
        return val
      }
      return 0
    },
    nde6 () {
      const nde = this.$store.getters.nde
      const module = nde.filter(x => x.module === ' Module 6  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 35))
      if (val !== null) {
        return val
      }
      return 0
    },
    dbe2 () {
      const dbe = this.$store.getters.dbe
      const module = dbe.filter(x => x.module === ' Decision Making  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 10))
      if (val !== null) {
        return val
      }
      return 0
    },
    dbe3 () {
      const dbe = this.$store.getters.dbe
      const module = dbe.filter(x => x.module === ' Manufacturing and Trade  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 9))
      if (val !== null) {
        return val
      }
      return 0
    },
    fibIntro () {
      const fib = this.$store.getters.fib
      const module = fib.filter(x => x.module === ' Introduction  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 2))
      if (val !== null) {
        return val
      }
      return 0
    },
    fib1 () {
      const fib = this.$store.getters.fib
      const module = fib.filter(x => x.module === ' Episode 1  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 6))
      if (val !== null) {
        return val
      }
      return 0
    },
    ebtn3 () {
      const ebtn = this.$store.getters.ebtn
      const module = ebtn.filter(x => x.module === ' 3. Related Changes  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 2))
      if (val !== null) {
        return val
      }
      return 0
    },
    ebtn4 () {
      const ebtn = this.$store.getters.ebtn
      const module = ebtn.filter(x => x.module === ' 4. Line Graphs  ')
      // return nde

      Array.prototype.sum = function (prop) {
          var total = 0
          for ( var i = 0, _len = this.length; i < _len; i++ ) {
              total += this[i][prop]
          }
          return total
      }

      const val = Math.floor((module.sum("percent") / 2))
      if (val !== null) {
        return val
      }
      return 0
    },
  }
}
</script>

<style lang="css">
</style>



