<script>
import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  props: {
    chartdata: {
      type: Object,
      default: null
    },
    options: {
      type: Object,
      default: null
    }
  },
  mounted () {
    this.renderChart({
      labels: ['', '', '', '', '', ''],
      datasets: [
        {
          label: 'Data One',
          backgroundColor: '#70B9F7',
          data: this.progressBarData.map(function(value) {
            return value >= 20 ? value : null
          }),
        },
        {
          label: 'Data Two',
          backgroundColor: '#E69672',
          data: this.progressBarData.map(function(value) {
            return value < 20 ? value : null
          }),
        }
      ]},
      {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false
        },
        tooltips: {
          callbacks: {
            label: function(tooltipItem) {
              return tooltipItem.yLabel;
            }
          }
        },
        scales: {
          xAxes: [{
            display: false,
            barThickness: 35,
            minBarLength: 2,
            stacked: true
          }],
          yAxes: [{
            display: false,
            stacked: true
          }]
        }
      }
    )
  },
  computed: {
    progressBarData () {
      // const wss = this.$store.state.stateCall1Call2["call-2"].original.data.wss.raw_value
      // const mt = this.$store.state.stateCall1Call2["call-2"].original.data.mt.raw_value
      // const sr = this.$store.state.stateCall1Call2["call-2"].original.data.sr.raw_value
      // const mic = this.$store.state.stateCall1Call2["call-2"].original.data.mic.raw_value
      // const repeats = this.$store.state.stateCall1Call2["call-2"].original.data.repeats.raw_value
      // const headphone = this.$store.state.stateCall1Call2["call-2"].original.data.headphone.raw_value

      const wss = 20
      const mt = 19
      const sr = 23
      const mic = 25
      const repeats = 16
      const headphone = 15

      const arrayData = [wss, mt, sr, mic, repeats, headphone]

      return arrayData
    }
  }
}
</script>

<style>
</style>



