<template lang="html">
  <div class="table__list">
    <div class="NocoachAvailable--global" v-if="!sessionListCheck">
       <div class="NoAvailable">
          <p>There's no upcoming session</p>
       </div>
    </div>
    <table style="width:100%" v-if="sessionListCheck">
      <tr>
        <th>Transaction</th>
        <th>Student</th>
        <th>Session Date</th>
        <th>Recorded Session</th>
      </tr>
      <tr v-for="(data, i) in sortedItems">
        <td align="center">{{ data.created_at | formatDate }}</td>
        <td align="center">{{ data.student_name }}</td>
        <td align="center">{{ data.date | formatDate }}, {{ data.start_time | formatTime }} - {{ data.end_time | formatTime }}</td>
        <td><div class="semi__btn">Check Availibility</div></td>
      </tr>
    </table>
  </div>
</template>

<script>
import { MomentFilter } from '../../../components/mixins/MomentFilter.js'
import axios from '../../../store/axios-auth'

export default {
  mixins: [MomentFilter],
  data () {
    return {
      historySessions: []
    }
  },
  mounted () {
    axios({
      method: 'GET',
      url: '/session/history/',
      headers: {Authorization: localStorage.getItem('token')}
     })
     .then(res => {
       console.log(res);
       this.historySessions = res.data.new_appointment
     })
     .catch(function (error) {
       console.log(error);
     });
  },
  computed: {
    sortedItems() {
      function compare(a, b) {
        if (a.date < b.date)
          return -1;
        if (a.date > b.date)
          return 1;
        return 0;
      }

      return this.historySessions.sort(compare);
    },
    sessionListCheck () {
      if (this.sortedItems != 0) {
        return true
      } else {
        return false
      }
    },
  }
}
</script>

<style lang="scss">
</style>



