<template lang="html">
  <div class="cert__prog">
    <div class="view__header">
      <div class="view__header__title">
        <div>
          <h3>C2 Progress</h3>
          <div class="blue__circle--light"></div>
        </div>
      </div>
    </div>
    <div class="cert__prog__container">
      <div class="cert__prog__study">
        <ul>
          <li :class="[ sessionTabs === 'nde' ? 'tab--active' : '']" @click="sessionTabs='nde'">
            <a>
              New Dynamic English
            </a>
          </li>
          <li :class="[ sessionTabs === 'dbe' ? 'tab--active' : '']" @click="sessionTabs='dbe'">
            <a>
              Dynamic Bussines English
            </a>
          </li>
          <li :class="[ sessionTabs === 'dial' ? 'tab--active' : '']" @click="sessionTabs='dial'">
            <a>
              Dialogue
            </a>
          </li>
          <li :class="[ sessionTabs === 'ebtn' ? 'tab--active' : '']" @click="sessionTabs='ebtn'">
            <a>
              English By The Number
            </a>
          </li>
          <li :class="[ sessionTabs === 'ali' ? 'tab--active' : '']" @click="sessionTabs='ali'">
            <a>
              Advanced Listening
            </a>
          </li>
        </ul>
      </div>
      <div class="cert__prog__unit">
        <div class="unit__container" v-if="sessionTabs === 'nde' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Module 8
            </div>
            <div class="unit__card__perc">
              {{ nde8 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'dbe' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 6
            </div>
            <div class="unit__card__perc">
              {{ dbe6 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'dial' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 3
            </div>
            <div class="unit__card__perc">
              {{ dial3 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'ebtn' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 7
            </div>
            <div class="unit__card__perc">
              {{ ebtn7 }}%
            </div>
          </div>
        </div>
        <div class="unit__container" v-if="sessionTabs === 'ali' ">
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 4
            </div>
            <div class="unit__card__perc">
              {{ ali4 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 5
            </div>
            <div class="unit__card__perc">
              {{ ali5 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 6
            </div>
            <div class="unit__card__perc">
              {{ ali6 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 7
            </div>
            <div class="unit__card__perc">
              {{ ali7 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 8
            </div>
            <div class="unit__card__perc">
              {{ ali8 }}%
            </div>
          </div>
          <div class="unit__card">
            <div class="unit__card__label">
              Unit 9
            </div>
            <div class="unit__card__perc">
              {{ ali9 }}%
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import CertificationUnit from './CertificationUnit'

export default {
  components: {
    // CertificationUnit
  },
  data() {
    return {
      sessionTabs: 'nde'
    }
  },
  computed: {
    nde8 () {
      const nde = this.$store.getters.nde
      const module = nde.filter(x => x.module === ' Module 8  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 35))
      if (val !== null) {
        return val
      }
      return 0
    },
    dbe6 () {
      const dbe = this.$store.getters.dbe
      const module = dbe.filter(x => x.module === ' Work Experience  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 35))
      if (val !== null) {
        return val
      }
      return 0
    },
    dial3 () {
      const dial = this.$store.getters.dial
      const module = dial.filter(x => x.module === ' Issues on Aging  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 18))
      if (val !== null) {
        return val
      }
      return 0
    },
    ebtn7 () {
      const ebtn = this.$store.getters.ebtn
      const module = ebtn.filter(x => x.module === ' 7. Review  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 3))
      if (val !== null) {
        return val
      }
      return 0
    },
    ali4 () {
      const ali = this.$store.getters.ali
      const module = ali.filter(x => x.module === ' How to Give a Lecture  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 56))
      if (val !== null) {
        return val
      }
      return 0
    },
    ali5 () {
      const ali = this.$store.getters.ali
      const module = ali.filter(x => x.module === ' The History of Slavery  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 32))
      if (val !== null) {
        return val
      }
      return 0
    },
    ali6 () {
      const ali = this.$store.getters.ali
      const module = ali.filter(x => x.module === ' The Geology of Volcanoes  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 8))
      if (val !== null) {
        return val
      }
      return 0
    },
    ali7 () {
      const ali = this.$store.getters.ali
      const module = ali.filter(x => x.module === ' The Invention of the Airplane  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 52))
      if (val !== null) {
        return val
      }
      return 0
    },
    ali8 () {
      const ali = this.$store.getters.ali
      const module = ali.filter(x => x.module === ' Principles of Child Psychiatry  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 28))
      if (val !== null) {
        return val
      }
      return 0
    },
    ali9 () {
      const ali = this.$store.getters.ali
      const module = ali.filter(x => x.module === ' Women and Social Reform  ')
      // return nde

      const val = Math.floor((module.sum("percent") / 16))
      if (val !== null) {
        return val
      }
      return 0
    }
  }
}
</script>

<style lang="css">
</style>



