<template lang="html">
  <div class="cert__plan">
    <div class="view__header">
      <div class="view__header__title">
        <div>
          <h3>Certificate Plan</h3>
          <div class="blue__circle--light"></div>
        </div>
      </div>
    </div>
    <!-- <div class="NocoachAvailable--global">
       <div class="NoAvailable">
          <p>There's no data</p>
       </div>
    </div> -->
    <div class="cert__plan__container">
      <div class="cert__plan__card"
        v-for="(data, index) in certLevelArray"
        :class="{ 'active': activeIndex === index }"
        :key="index"
        @click="setActive(index)"
      >
        <div class="cert__plan__card__level">
          {{ data[0] }}
        </div>
        <div class="cert__plan__card__perc">
          {{ data[1] }}%
        </div>
        <div class="cert__plan__card__arrow">
          <div class="arrow__circle">
            <!-- <v-icon>keyboard_arrow_down</v-icon> -->
          </div>
        </div>
      </div>
    </div>
    <div class="cert__prog__cmp"  v-if="certLevelArray !== 0">
      <CertProgA1 v-if="activeIndex === 0" />
      <CertProgA2 v-if="activeIndex === 1" />
      <CertProgB1 v-if="activeIndex === 2" />
      <CertProgB2 v-if="activeIndex === 3" />
      <CertProgC1 v-if="activeIndex === 4" />
      <CertProgC2 v-if="activeIndex === 5" />
    </div>
  </div>
</template>

<script>
import CertProgA1 from './certification-progress/cert_prog_A1'
import CertProgA2 from './certification-progress/cert_prog_A2'
import CertProgB1 from './certification-progress/cert_prog_B1'
import CertProgB2 from './certification-progress/cert_prog_B2'
import CertProgC1 from './certification-progress/cert_prog_C1'
import CertProgC2 from './certification-progress/cert_prog_C2'

export default {
  components: {
    CertProgA1,
    CertProgA2,
    CertProgB1,
    CertProgB2,
    CertProgC1,
    CertProgC2
  },
  data() {
    return {
      activeIndex: 0,
    }
  },
  computed: {
    getDataCall1 () {
      const stateCall1 = this.$store.state.stateCall1Call2
      if (stateCall1 == null) {
        return 0
      } else {
        return this.$store.state.stateCall1Call2["call-1"]
      }
    },
    getDataCall2 () {
      const call2 = this.$store.state.stateCall1Call2
      if (call2 == null) {
        return 0
      } else {
        return this.$store.state.stateCall1Call2["call-2"].original.data
      }
      // return this.$store.state.stateCall1Call2["call-2"].original.data
    },
    certLevelArray () {
      // this.certLevel = this.getDataCall2.cert_level_completion
      var obj = this.getDataCall1.certLevelCompletion
      if (obj !== undefined) {
        var result = Object.keys(obj).map(function(key) {
          return [String(key), obj[key]];
        });
      } else {
        return 0
      }

      return result
    }
  },
  methods: {
  	setActive(i) { this.activeIndex = i }
  }
}
</script>

<style lang="scss">
</style>



