<template lang="html">
  <v-dialog width="1020" v-model="showDialog" persistent>
    <v-card style="background-color:#fff;border-radius: 3px;">
      <v-btn icon light @click="changeDialogState">
        <v-icon>close</v-icon>
      </v-btn>
      <div class="upcoming__report">
        <div class="student__progress__content">
          <div class="flex--column width--50perc">
            <StudyLevel />
            <ProgressBar />
          </div>
          <CertificatePlan />
        </div>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
import StudyLevel from '../../../student-components/studentProgressReport/StudyLevel'
import ProgressBar from '../../../student-components/studentProgressReport/ProgressBar'
import CertificatePlan from '../../../student-components/studentProgressReport/CertificatePlan'

export default {
  components: {
    StudyLevel,
    ProgressBar,
    CertificatePlan,
  },
  data () {
    return {

    }
  },
  computed: {
    showDialog () {
      return this.$store.state.showDialog
    },
    //FOR LOTTIE
    handleAnimation: function (anim) {
       this.anim = anim;
    },
  },
  methods: {
    // ACTION FOR CANCEL BUTTON
    changeDialogState () {
      this.$store.commit('toggleDialogFalse')
    },
  },
  watch: {

  }
}
</script>

<style lang="scss" scoped>
  @import '../../../views/studentProgressReport/StudentProgressReport.scss';

  .BXcoachProf {

    .CoachProfile__rating {
      width: 83% !important;
    }
  }
  .bxDate {
    padding: 10px 40px !important;
  }
  .v-picker {
    background-color: #fff !important;
    border: 1px solid #ddd !important;
  }
  .overflow--ellipsis {
    width: 95%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  .upcoming__report {
    padding: 10px 20px;
  }
</style>



