<template lang="html">
  <div class="study__level">
    <div class="view__header">
      <div class="view__header__title">
        <div>
          <h3>Study Level</h3>
          <div class="blue__circle--light"></div>
        </div>
      </div>
    </div>
    <div class="study__level__content">
      <div class="study__level__card">
        <div class="study__level__card__label">
          Last PT
        </div>
        <div class="study__level__card__value">
          {{ getDataCall2.last_pt_score }}
        </div>
      </div>
      <div class="study__level__card">
        <div class="study__level__card__label">
          Hours/week
        </div>
        <div class="study__level__card__value">
          {{ getDataCall2.hours_per_week.raw_value }}
        </div>
      </div>
      <div class="study__level__card">
        <div class="study__level__card__label">
          Weighted Study Score
        </div>
        <div class="study__level__card__value">
          {{ getDataCall2.wss.raw_value }}
        </div>
      </div>
      <div class="study__level__card">
        <div class="study__level__card__label">
          Initial Placement Level
        </div>
        <div class="study__level__card__value">
          {{ getDataCall2.initial_pt_score }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    getDataCall2 () {
      const call2 = this.$store.state.stateCall1Call2
      if (call2 == null) {
        return 0
      } else {
        return this.$store.state.stateCall1Call2["call-2"].original.data
      }
      // return this.$store.state.stateCall1Call2["call-2"].original.data
    }
  },
}
</script>

<style lang="scss">
</style>



